@import url(https://fonts.googleapis.com/css2?family=Archivo+Black&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: white;
}

.App {
  height: 100vh;
  overflow-y: hidden;
}

.Main-text {
  font-family: 'Archivo Black', sans-serif;
  font-size: 64px;
  line-height: 70px;

  color: #744A96;
}

.App-header {
  min-height: 100vh;
  background-color: #BF8BDF;
  width: 2800px;
  padding-left: min(20vw, 150px);
  display: flex;
  flex-direction: row;
  align-items: center;
  /* justify-content: center; */
}

.Greeting {
  /* HI, I’M CHLOE AND I MAKE THINGS */
  width: 300px;
  margin-right: 200px;

  -webkit-animation: 1s ease-out 0s 1 slideInFromLeft;

          animation: 1s ease-out 0s 1 slideInFromLeft;
}

.White {
  color: white;
}

@-webkit-keyframes slideInFromLeft {
  0% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes slideInFromLeft {
  0% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

.End {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 630px;

  margin-left: 120px;
}

.Links {
  width: 450px;
  text-align: right;
}

.Link {
  font-family: 'Lato', sans-serif;
  font-size: 24px;
  color: white;
}
.Card {
  position: relative;
  min-width: 320px;
  min-height: 442px;
  margin: 0px 40px;
}

@media screen and (min-height: 800px) {
  .top {
    margin-bottom: 179px;
    -webkit-animation: 1s ease-out 0s 1 slideInFromBottom;
            animation: 1s ease-out 0s 1 slideInFromBottom;
  }
  .bottom {
    margin-top: 179px;
    -webkit-animation: 1s ease-out 0s 1 slideInFromTop;
            animation: 1s ease-out 0s 1 slideInFromTop;
  }
}

@-webkit-keyframes slideInFromTop {
  0% {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes slideInFromTop {
  0% {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@-webkit-keyframes slideInFromBottom {
  0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes slideInFromBottom {
  0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

.Card-rectangle {
  position: absolute;
  left: 0%;
  right: 0%;
  top: 0%;
  bottom: 0%;

  background: #FFFFFF;
  box-shadow: 15px 15px 20px rgba(0, 0, 0, 0.2);
  border-radius: 30px;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.Card-title {
  margin-top: 15%;
  width: 90%;

  font-family: 'Archivo Black', sans-serif;
  font-size: 24px;
  line-height: 26px;
  text-align: center;

  color: #744A96;
}

.Card-tags {
  margin-top: 5px;

  font-family: 'Lato', sans-serif;
  font-size: 18px;
  line-height: 22px;
  text-align: center;

  color: #BF9FDF;
}

.Card-image {
  width: 85%;
  height: 60%;
  margin-top: auto;
  margin-bottom: 7.5%;

  background-size: cover;
  border-radius: 20px;
  box-shadow: inset 0px 0px 4px 4px rgba(0, 0, 0, 0.25);
}
